/* eslint-disable */
import type { BaseHttpRequest } from './core/BaseHttpRequest';
import type { OpenAPIConfig } from './core/OpenAPI';
import { FetchHttpRequest } from './core/FetchHttpRequest';

import { AnsattService } from './services/AnsattService';
import { BrukerService } from './services/BrukerService';
import { DelMedBrukerService } from './services/DelMedBrukerService';
import { DialogenService } from './services/DialogenService';
import { HistorikkService } from './services/HistorikkService';
import { InnsatsgrupperService } from './services/InnsatsgrupperService';
import { InternalService } from './services/InternalService';
import { SanityService } from './services/SanityService';
import { TiltaksgjennomforingerService } from './services/TiltaksgjennomforingerService';
import { TiltakstyperService } from './services/TiltakstyperService';
import { VeilederService } from './services/VeilederService';

type HttpRequestConstructor = new (config: OpenAPIConfig) => BaseHttpRequest;

export class MulighetsrommetClient {

    public readonly ansatt: AnsattService;
    public readonly bruker: BrukerService;
    public readonly delMedBruker: DelMedBrukerService;
    public readonly dialogen: DialogenService;
    public readonly historikk: HistorikkService;
    public readonly innsatsgrupper: InnsatsgrupperService;
    public readonly internal: InternalService;
    public readonly sanity: SanityService;
    public readonly tiltaksgjennomforinger: TiltaksgjennomforingerService;
    public readonly tiltakstyper: TiltakstyperService;
    public readonly veileder: VeilederService;

    public readonly request: BaseHttpRequest;

    constructor(config?: Partial<OpenAPIConfig>, HttpRequest: HttpRequestConstructor = FetchHttpRequest) {
        this.request = new HttpRequest({
            BASE: config?.BASE ?? '',
            VERSION: config?.VERSION ?? '1.0.0',
            WITH_CREDENTIALS: config?.WITH_CREDENTIALS ?? false,
            CREDENTIALS: config?.CREDENTIALS ?? 'include',
            TOKEN: config?.TOKEN,
            USERNAME: config?.USERNAME,
            PASSWORD: config?.PASSWORD,
            HEADERS: config?.HEADERS,
            ENCODE_PATH: config?.ENCODE_PATH,
        });

        this.ansatt = new AnsattService(this.request);
        this.bruker = new BrukerService(this.request);
        this.delMedBruker = new DelMedBrukerService(this.request);
        this.dialogen = new DialogenService(this.request);
        this.historikk = new HistorikkService(this.request);
        this.innsatsgrupper = new InnsatsgrupperService(this.request);
        this.internal = new InternalService(this.request);
        this.sanity = new SanityService(this.request);
        this.tiltaksgjennomforinger = new TiltaksgjennomforingerService(this.request);
        this.tiltakstyper = new TiltakstyperService(this.request);
        this.veileder = new VeilederService(this.request);
    }
}

