/* eslint-disable */
import type { PaginertTiltakstype } from '../models/PaginertTiltakstype';
import type { Tiltaksgjennomforing } from '../models/Tiltaksgjennomforing';
import type { Tiltakskode } from '../models/Tiltakskode';
import type { Tiltakstype } from '../models/Tiltakstype';

import type { CancelablePromise } from '../core/CancelablePromise';
import type { BaseHttpRequest } from '../core/BaseHttpRequest';

export class TiltakstyperService {

    constructor(public readonly httpRequest: BaseHttpRequest) {}

    /**
     * @returns PaginertTiltakstype Object of paginerte tiltakstyper.
     * @throws ApiError
     */
    public getTiltakstyper({
        search,
        innsatsgrupper,
    }: {
        /**
         * Search for tiltakstyper
         */
        search?: string,
        /**
         * Innsatsgruppefilter
         */
        innsatsgrupper?: Array<number>,
    }): CancelablePromise<PaginertTiltakstype> {
        return this.httpRequest.request({
            method: 'GET',
            url: '/api/v1/tiltakstyper',
            query: {
                'search': search,
                'innsatsgrupper': innsatsgrupper,
            },
        });
    }

    /**
     * @returns Tiltakstype the specified tiltakstype
     * @throws ApiError
     */
    public getTiltakstypeById({
        id,
    }: {
        /**
         * Id for tiltakstype
         */
        id?: string,
    }): CancelablePromise<Tiltakstype> {
        return this.httpRequest.request({
            method: 'GET',
            url: '/api/v1/tiltakstyper/{id}',
            query: {
                'id': id,
            },
            errors: {
                404: `The specified tiltakstype was not found.`,
            },
        });
    }

    /**
     * @returns Tiltakstype the specified tiltakstype.
     * @throws ApiError
     */
    public getTiltakstype({
        tiltakskode,
    }: {
        /**
         * Tiltakskode
         */
        tiltakskode: Tiltakskode,
    }): CancelablePromise<Tiltakstype> {
        return this.httpRequest.request({
            method: 'GET',
            url: '/api/v1/tiltakstyper/{tiltakskode}',
            path: {
                'tiltakskode': tiltakskode,
            },
            errors: {
                404: `The specified tiltakstype was not found.`,
            },
        });
    }

    /**
     * @returns Tiltaksgjennomforing An array of tiltaksgjennomføringer for specified tiltakskode.
     * @throws ApiError
     */
    public getTiltaksgjennomforingerByTiltakskode({
        tiltakskode,
    }: {
        /**
         * Tiltakskode
         */
        tiltakskode: Tiltakskode,
    }): CancelablePromise<Array<Tiltaksgjennomforing>> {
        return this.httpRequest.request({
            method: 'GET',
            url: '/api/v1/tiltakstyper/{tiltakskode}/tiltaksgjennomforinger',
            path: {
                'tiltakskode': tiltakskode,
            },
            errors: {
                404: `The specified tiltakstype was not found.`,
            },
        });
    }

}
