import type { PaginertTiltaksgjennomforing } from '../models/PaginertTiltaksgjennomforing';
import type { Tiltaksgjennomforing } from '../models/Tiltaksgjennomforing';

import type { CancelablePromise } from '../core/CancelablePromise';
import type { BaseHttpRequest } from '../core/BaseHttpRequest';

export class TiltaksgjennomforingerService {

    constructor(public readonly httpRequest: BaseHttpRequest) {}

    /**
     * @returns PaginertTiltaksgjennomforing Object of paginated tiltaksgjennomføringer.
     * @throws ApiError
     */
    public getTiltaksgjennomforinger({
        search,
        innsatsgrupper,
        tiltakstyper,
    }: {
        /**
         * Search for tiltaksgjennomføringer
         */
        search?: string,
        /**
         * Innsatsgruppefilter
         */
        innsatsgrupper?: Array<number>,
        /**
         * Tiltakstypefilter
         */
        tiltakstyper?: Array<number>,
    }): CancelablePromise<PaginertTiltaksgjennomforing> {
        return this.httpRequest.request({
            method: 'GET',
            url: '/api/v1/tiltaksgjennomforinger',
            query: {
                'search': search,
                'innsatsgrupper': innsatsgrupper,
                'tiltakstyper': tiltakstyper,
            },
        });
    }

    /**
     * @returns Tiltaksgjennomforing The specified tiltaksgjennomføring.
     * @throws ApiError
     */
    public getTiltaksgjennomforing({
        id,
    }: {
        /**
         * ID
         */
        id: number,
    }): CancelablePromise<Tiltaksgjennomforing> {
        return this.httpRequest.request({
            method: 'GET',
            url: '/api/v1/tiltaksgjennomforinger/{id}',
            path: {
                'id': id,
            },
            errors: {
                404: `The specified tiltaksgjennomføring was not found.`,
            },
        });
    }

}
