/* eslint-disable */
import type { DialogRequest } from '../models/DialogRequest';
import type { DialogResponse } from '../models/DialogResponse';

import type { CancelablePromise } from '../core/CancelablePromise';
import type { BaseHttpRequest } from '../core/BaseHttpRequest';

export class DialogenService {

    constructor(public readonly httpRequest: BaseHttpRequest) {}

    /**
     * @returns DialogResponse Dialog response
     * @throws ApiError
     */
    public delMedDialogen({
        requestBody,
    }: {
        requestBody?: DialogRequest,
    }): CancelablePromise<DialogResponse> {
        return this.httpRequest.request({
            method: 'POST',
            url: '/api/v1/dialog',
            body: requestBody,
            mediaType: 'application/json',
            errors: {
                409: `Sending message to Dialogen failed`,
            },
        });
    }

}
