import type { DelMedBruker } from '../models/DelMedBruker';

import type { CancelablePromise } from '../core/CancelablePromise';
import type { BaseHttpRequest } from '../core/BaseHttpRequest';

export class DelMedBrukerService {

    constructor(public readonly httpRequest: BaseHttpRequest) {}

    /**
     * @returns any Last entry in database about share with user
     * @throws ApiError
     */
    public getDelMedBruker({
        tiltaksnummer,
        fnr,
    }: {
        /**
         * Tiltaksnummer for et tiltak
         */
        tiltaksnummer: string,
        /**
         * Brukers fnr
         */
        fnr?: string,
    }): CancelablePromise<any> {
        return this.httpRequest.request({
            method: 'GET',
            url: '/api/v1/delMedBruker/{tiltaksnummer}',
            path: {
                'tiltaksnummer': tiltaksnummer,
            },
            query: {
                'fnr': fnr,
            },
        });
    }

    /**
     * @returns any Stored data from insertion in database
     * @throws ApiError
     */
    public postDelMedBruker({
        tiltaksnummer,
        requestBody,
    }: {
        /**
         * Tiltaksnummer for et tiltak
         */
        tiltaksnummer: string,
        requestBody?: DelMedBruker,
    }): CancelablePromise<any> {
        return this.httpRequest.request({
            method: 'POST',
            url: '/api/v1/delMedBruker',
            path: {
                'tiltaksnummer': tiltaksnummer,
            },
            body: requestBody,
            mediaType: 'application/json',
        });
    }

}
